// ANIMATION - fade out
@keyframes appLoaderFadeOut { from { opacity: 1.0; } to { opacity: 0; } }

.app-loader {
	position: fixed;
	top: 0;
	inset-inline-start: 0;
	bottom: 0;
	inset-inline-end: 0;
	background: $white;
	z-index: $app-loader-zindex;
	height: 100vh;
	height: 100dvh;
	opacity: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	
	&.fading {
		opacity: 0;
		animation: appLoaderFadeOut .2s ease;
	}
	&.loaded {
		display: none;
		
		& .spinner {
			animation: none;
		}
	}
}